/***********************************************/
/*Styles Balance History*/
/***********************************************/
.title__main {
  color: #000;
  font-weight: 600;
  font-size: 1.2rem;
  display: inline-block;
  font-family: "Lato" !important; }

.chart__container {
  width: 100%;
  margin-top: 60px;
  height: 100%;
  text-align: center;
  display: inline-block; }

.filters__container {
  position: absolute;
  top: 1rem;
  right: 2rem;
  height: auto;
  border-radius: 4px; }
  .filters__container .filter-c {
    height: auto;
    padding: 5px 10px;
    font-weight: 400;
    border-right: 1px solid #b3b3b3;
    background-color: #e4e4e4;
    text-align: center;
    display: inline-block;
    vertical-align: top;
    font-size: .75rem;
    cursor: pointer;
    color: #777;
    transition: 0.6s;
    text-decoration: none !important; }
  .filters__container .filter-c:last-child {
    border-right: none; }
  .filters__container .filter-c:hover {
    transition: 0.6s;
    background-color: #333;
    color: #fff !important; }

.filter__main {
  display: inline-block;
  height: auto;
  vertical-align: top; }
  .filter__main .name-filter {
    display: inline-block;
    height: auto;
    vertical-align: middle;
    font-size: .95rem;
    color: #676767;
    font-weight: 600;
    letter-spacing: normal; }
  .filter__main .input-filter {
    display: inline-block;
    height: auto;
    vertical-align: middle;
    font-size: .95rem;
    color: #676767;
    border: 1px solid #676767;
    padding: 5px 15px;
    margin-left: 15px;
    font-weight: 400;
    border-radius: 4px;
    text-align: center;
    letter-spacing: normal;
    margin-right: 15px; }
  .filter__main .input-filter:last-child {
    margin-right: 0; }

.recharts-default-legend {
  text-align: left !important; }
