/***********************************************/
/*Styles Assets*/
/***********************************************/
.title__main {
  color: #000;
  font-weight: 600;
  font-size: 1.2rem;
  display: inline-block;
  font-family: "Lato" !important; }
  .title__main .mini-title {
    display: inline-block;
    vertical-align: top;
    height: auto;
    margin-left: 15px;
    font-size: .9rem;
    color: #000;
    letter-spacing: normal;
    font-weight: 400;
    vertical-align: middle; }

.title__asset {
  width: 100%;
  margin-top: 64px;
  height: auto;
  padding: 40px;
  background-color: #dee2e6; }

.nav-tabs .nav-link {
  border: 1px solid transparent;
  border-top-left-radius: .25rem;
  border-top-right-radius: .25rem;
  margin-left: 0%;
  background-color: #eaeaea;
  margin-right: 4%;
  width: 48%;
  text-align: center;
  font-size: 1.1rem;
  color: #908e8e;
  font-weight: 400;
  letter-spacing: normal; }

.nav-tabs .nav-link:last-child {
  margin-right: 0 !important; }

.container__tab {
  width: 100%;
  padding: 4%;
  border-bottom: 1px solid #dee2e6;
  border-left: 1px solid #dee2e6;
  border-right: 1px solid #dee2e6; }

.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
  color: #000;
  font-weight: 700;
  background-color: #fff;
  border-color: #dee2e6 #dee2e6 #fff; }

.detail__title {
  font-size: .9rem;
  color: #676767;
  height: auto;
  width: 100%;
  height: auto;
  letter-spacing: normal;
  text-align: left;
  font-weight: 400; }

.detail__value {
  font-size: .91rem;
  color: #000;
  height: auto;
  width: 100%;
  height: auto;
  text-align: center;
  letter-spacing: normal;
  font-weight: 600; }

.description__general {
  width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: top;
  background-color: red; }

.content__general {
  font-weight: 400;
  color: #000;
  text-align: justify;
  letter-spacing: normal;
  line-height: 1.50;
  height: auto;
  margin: 20px 0;
  font-size: .95rem; }
